













import { Component, Vue, Watch } from 'vue-property-decorator';
import { routes } from '@/router';
import { RouteProperty } from '@/interface/routeProperty';
import { mapGetters } from 'vuex';

@Component({
  computed: {
    ...mapGetters({
      activeRoute: 'activeRoute',
    }),
  },
})
export default class Main extends Vue {
  private activeRoute!: string;

  public created() {
    this.$store.dispatch('startPolling');
    this.$store.dispatch('getTreeStructure');
  }

  get pageRoutes(): RouteProperty[] {
    return routes[0].children
      ? routes[0].children.map((o) => ({
          ...o,
          class: this.activeRoute === o.path ? 'is-active' : '',
        }))
      : [];
  }
}
